.workWithMe,
.contactMe,
.socialProfiles,
.socialUl,
.copyrightInfo {
  text-align: center;
  color: white;
}

footer {
  padding: 0 2em;
  background: #6c63ff;
}

.workWithMe {
  padding: 2em 0 1em 0;
  font-size: 1.5em;
  font-weight: 700;
}

.contactMe {
  font-size: 1.2em;
  padding-bottom: 1em;
  font-weight: 500;
}

.footerHr {
  height: 0.5rem;
  border-radius: 5px;
  background-color: white;
  border: none;
  position: relative;
  margin: auto;
  width: 80%;
  margin-bottom: 1em;
}

.socialUl {
  display: flex;
  justify-content: center;
  list-style: none;
}

.socialProfiles {
  font-size: 1.5em;
  font-weight: 700;
}

.socialUl li {
  margin: 0 1em;
  font-size: 1.5em;
  padding: 1em 0;
  font-weight: 400;
  cursor: pointer;
}

.copyrightInfo {
  font-size: 0.8em;
  font-weight: 400;
  padding-bottom: 1em;
}
