.home {
  padding: 1em 2em;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  overflow: hidden;
}

.landingIMG {
  width: 50%;
  pointer-events: none;
  position: relative;
  left: 5em;
  transform: scale(1.15);
  animation: float 500ms infinite alternate;
}

.homeHeader {
  margin-top: 1em;
  padding: 0.2em 0;
  font-size: 3.5em;
  color: #464353;
  opacity: 0.8;
}

.typical {
  padding-bottom: 0.5em;
  font-size: 1.5em;
  color: #464353;
  opacity: 0.7;
}

.homeParagraph {
  line-height: 1.5;
  z-index: 10;
  text-align: justify;
  font-size: 1.2em;
  font-weight: 400;
  padding-bottom: 1em;
  color: #464353;
  opacity: 0.9;
}

.homeChat {
  border: none;
  border-radius: 5px;
  background-color: #6c63ff;
  color: white;
  cursor: pointer;
  font-size: 1.1em;
  padding: 0.5rem;
  font-weight: 400;
}

@media (max-width: 768px) {
  .home {
    flex-direction: column;
  }
  .landingIMG {
    display: none;
  }
}

@keyframes float {
  from {
    transform: translateY(-10px);
  }
  to {
    transform: translateY(0px);
  }
}
