.topNav {
  display: flex;
  align-items: center;
  position: relative;
  height: 12vh;
  background: #6c63ff;
  color: white;
}

.topHeader,
.topIcon {
  position: absolute;
  font-size: 2em;
}

.topHeader {
  left: 1em;
}

.topIcon {
  right: 1em;
  cursor: pointer;
}

.sideBar {
  position: fixed;
  top: 0;
  width: 30%;
  height: 100vh;
  background: #f0f0ff;
  display: grid;
  place-items: center;
  transition: all 300ms ease;
  overflow: hidden;
  z-index: 10;
}

.sideBar.hidden {
  width: 0;
  pointer-events: none;
}

.sideBarUl {
  padding: 0.5em 0;
  list-style: none;
}

.navLinks {
  padding: 0.5em 0;
  font-size: 1.5em;
}

.navLinks i {
  margin-right: 0.5em;
}

.navLinks a {
  font-weight: 600;
  text-decoration: none;
  padding-bottom: 5px;
  color: #6c63ff;
}

.navLinks a:hover {
  transition: border 200ms;
  color: #464353;
}

@media (max-width: 768px) {
  .sideBar {
    width: 70%;
  }
}
