.aboutHeader,
.aboutUl {
  color: #464353;
}

.aboutHeader {
  padding: 1em 1em;
  font-size: 2.5em;
  opacity: 0.8;
}

.aboutUl {
  padding: 0 0 2em 0.8em;
  width: 80%;
  position: relative;
  margin: auto;
  list-style: none;
}

.aboutLi {
  font-weight: 400;
  font-size: 1.2em;
  text-align: justify;
  padding: 1em 0;
  line-height: 1.5;
}

.aboutLi::before {
  content: "\2022";
  color: #6c63ff;
  font-weight: bold;
  display: inline-block;
  width: 1em;
  margin-left: -1em;
}
