.myProjects {
  text-align: center;
  font-size: 2em;
  margin: 1.5em 2em;
  color: #464353;
}

.projectsGrid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 1rem;
  grid-template-rows: 1fr 1fr;
  position: relative;
  margin: 0 2em;
}

.projectItem {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 1rem;
  width: 30%;
  width: 100%;
  font-size: 1.2em;
  color: #464353;
}

.projectImage {
  border-radius: 5px;
  margin: 20px 0;
  width: 100%;
  cursor: pointer;
  transition: transform 300ms ease;
}

.projectImage:hover {
  transform: scale(1.1);
}

@media (max-width: 768px) {
  .projectsGrid {
    display: flex;
    flex-direction: column;
  }
}
