.errorWrapper {
  position: relative;
  margin: 0 auto;
  width: 90%;
  text-align: center;
}

.errorPng {
  padding-top: 2em;
  width: 100%;
  animation: float 500ms infinite alternate;
}

.errorParagraph {
  padding: 2em 0;
  font-size: 1.5em;
  color: #464353;
}

.text {
  opacity: 0.7;
  line-height: 1.5;
}

.backHome {
  background: #6c63ff;
  color: white;
  text-decoration: none;
  padding: 10px;
  border-radius: 5px;
  cursor: pointer;
}

@keyframes float {
  from {
    transform: translateY(-10px);
  }
  to {
    transform: translateY(0px);
  }
}
