@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700;800&display=swap);
.topNav {
  display: flex;
  align-items: center;
  position: relative;
  height: 12vh;
  background: #6c63ff;
  color: white;
}

.topHeader,
.topIcon {
  position: absolute;
  font-size: 2em;
}

.topHeader {
  left: 1em;
}

.topIcon {
  right: 1em;
  cursor: pointer;
}

.sideBar {
  position: fixed;
  top: 0;
  width: 30%;
  height: 100vh;
  background: #f0f0ff;
  display: grid;
  place-items: center;
  transition: all 300ms ease;
  overflow: hidden;
  z-index: 10;
}

.sideBar.hidden {
  width: 0;
  pointer-events: none;
}

.sideBarUl {
  padding: 0.5em 0;
  list-style: none;
}

.navLinks {
  padding: 0.5em 0;
  font-size: 1.5em;
}

.navLinks i {
  margin-right: 0.5em;
}

.navLinks a {
  font-weight: 600;
  text-decoration: none;
  padding-bottom: 5px;
  color: #6c63ff;
}

.navLinks a:hover {
  transition: border 200ms;
  color: #464353;
}

@media (max-width: 768px) {
  .sideBar {
    width: 70%;
  }
}

.home {
  padding: 1em 2em;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  overflow: hidden;
}

.landingIMG {
  width: 50%;
  pointer-events: none;
  position: relative;
  left: 5em;
  transform: scale(1.15);
  animation: float 500ms infinite alternate;
}

.homeHeader {
  margin-top: 1em;
  padding: 0.2em 0;
  font-size: 3.5em;
  color: #464353;
  opacity: 0.8;
}

.typical {
  padding-bottom: 0.5em;
  font-size: 1.5em;
  color: #464353;
  opacity: 0.7;
}

.homeParagraph {
  line-height: 1.5;
  z-index: 10;
  text-align: justify;
  font-size: 1.2em;
  font-weight: 400;
  padding-bottom: 1em;
  color: #464353;
  opacity: 0.9;
}

.homeChat {
  border: none;
  border-radius: 5px;
  background-color: #6c63ff;
  color: white;
  cursor: pointer;
  font-size: 1.1em;
  padding: 0.5rem;
  font-weight: 400;
}

@media (max-width: 768px) {
  .home {
    flex-direction: column;
  }
  .landingIMG {
    display: none;
  }
}

@keyframes float {
  from {
    transform: translateY(-10px);
  }
  to {
    transform: translateY(0px);
  }
}

.myProjects {
  text-align: center;
  font-size: 2em;
  margin: 1.5em 2em;
  color: #464353;
}

.projectsGrid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 1rem;
  gap: 1rem;
  grid-template-rows: 1fr 1fr;
  position: relative;
  margin: 0 2em;
}

.projectItem {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 1rem;
  width: 30%;
  width: 100%;
  font-size: 1.2em;
  color: #464353;
}

.projectImage {
  border-radius: 5px;
  margin: 20px 0;
  width: 100%;
  cursor: pointer;
  transition: transform 300ms ease;
}

.projectImage:hover {
  transform: scale(1.1);
}

@media (max-width: 768px) {
  .projectsGrid {
    display: flex;
    flex-direction: column;
  }
}

.aboutHeader,
.aboutUl {
  color: #464353;
}

.aboutHeader {
  padding: 1em 1em;
  font-size: 2.5em;
  opacity: 0.8;
}

.aboutUl {
  padding: 0 0 2em 0.8em;
  width: 80%;
  position: relative;
  margin: auto;
  list-style: none;
}

.aboutLi {
  font-weight: 400;
  font-size: 1.2em;
  text-align: justify;
  padding: 1em 0;
  line-height: 1.5;
}

.aboutLi::before {
  content: "\2022";
  color: #6c63ff;
  font-weight: bold;
  display: inline-block;
  width: 1em;
  margin-left: -1em;
}

.errorWrapper {
  position: relative;
  margin: 0 auto;
  width: 90%;
  text-align: center;
}

.errorPng {
  padding-top: 2em;
  width: 100%;
  animation: float 500ms infinite alternate;
}

.errorParagraph {
  padding: 2em 0;
  font-size: 1.5em;
  color: #464353;
}

.text {
  opacity: 0.7;
  line-height: 1.5;
}

.backHome {
  background: #6c63ff;
  color: white;
  text-decoration: none;
  padding: 10px;
  border-radius: 5px;
  cursor: pointer;
}

@keyframes float {
  from {
    transform: translateY(-10px);
  }
  to {
    transform: translateY(0px);
  }
}

.workWithMe,
.contactMe,
.socialProfiles,
.socialUl,
.copyrightInfo {
  text-align: center;
  color: white;
}

footer {
  padding: 0 2em;
  background: #6c63ff;
}

.workWithMe {
  padding: 2em 0 1em 0;
  font-size: 1.5em;
  font-weight: 700;
}

.contactMe {
  font-size: 1.2em;
  padding-bottom: 1em;
  font-weight: 500;
}

.footerHr {
  height: 0.5rem;
  border-radius: 5px;
  background-color: white;
  border: none;
  position: relative;
  margin: auto;
  width: 80%;
  margin-bottom: 1em;
}

.socialUl {
  display: flex;
  justify-content: center;
  list-style: none;
}

.socialProfiles {
  font-size: 1.5em;
  font-weight: 700;
}

.socialUl li {
  margin: 0 1em;
  font-size: 1.5em;
  padding: 1em 0;
  font-weight: 400;
  cursor: pointer;
}

.copyrightInfo {
  font-size: 0.8em;
  font-weight: 400;
  padding-bottom: 1em;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Poppins", sans-serif;
}

html {
  scroll-behavior: smooth;
}

body {
  background-color: #f0f0ff;
}

